import React from 'react';
import { useGame } from '../contexts/GameContext';
import './TimerDisplay.css';

interface TimerDisplayProps {
    totalTime?: number;
}

const TimerDisplay: React.FC<TimerDisplayProps> = ({ totalTime = 30 }) => {
    const { timer, role } = useGame();

    // Only show timer if role is host and timer is active and greater than 0
    if (role !== 'host' || timer === null || timer <= 0) {
        return null;
    }

    return (
        <div className="timer-numeric-display">
            {timer}
        </div>
    );
};

export default TimerDisplay;
