import React, { createContext, useContext, useState, useEffect, ReactNode, useRef } from 'react';
import { io, Socket } from 'socket.io-client';
import { WS_URL } from '../constants/config';
import { GameState, GamePart, Player, UserRole, Question } from '../types';

export interface GameContextType {
  // Connection
  socket: Socket | null;
  isConnected: boolean;

  // Room info
  roomCode: string | null;
  setRoomCode: (code: string | null) => void;

  // User info
  role: UserRole;
  setRole: (role: UserRole) => void;
  playerName: string;
  setPlayerName: (name: string) => void;
  playerId: string | null;
  setPlayerId: (id: string | null) => void;

  // Players list
  players: Player[];

  // Game state
  gameState: GameState;
  currentPart: GamePart | null;
  nextPart: GamePart | null; // For host preview
  currentPartIndex: number;
  totalParts: number;
  pickOrderState?: {
    sortedPlayerIds: string[];
    currentTurnIndex: number;
    takenPositions: Record<number, string>;
  };

  // Client answers (for current user)
  selectedAnswer: number | string | number[] | null;
  setSelectedAnswer: (answer: number | string | number[] | null) => void;

  // Admin controls
  isAdmin: boolean;
  setIsAdmin: (isAdmin: boolean) => void;

  // Timer
  timer: number | null;

  // Recent answer notifications
  recentAnswers: Array<{ playerId: string; timestamp: number }>;

  // Actions
  createRoom: () => void;
  joinRoom: (roomCode: string, playerName: string, broughtGift: boolean) => void;
  leaveRoom: () => void;
  submitAnswer: (answerIndex: number | string | number[]) => void;
  submitDraft: (answerIndex: number | string | number[]) => void;
  adminLogin: (password: string) => Promise<boolean>;
  resetTimer: (seconds: number) => void;
  advanceGame: () => void;
  submitPachinkoResults: (results: { playerId: string; bucketIndex: number }[]) => void;
  submitWager: (wager: number) => void;
}

const GameContext = createContext<GameContextType | undefined>(undefined);

export const useGame = () => {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
};

interface GameProviderProps {
  children: ReactNode;
}

export const GameProvider: React.FC<GameProviderProps> = ({ children }) => {
  const [socket, setSocket] = useState<Socket | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const [roomCode, setRoomCode] = useState<string | null>(null);
  const [role, setRole] = useState<UserRole>(null);
  const [playerName, setPlayerName] = useState<string>('');
  const [playerId, setPlayerId] = useState<string | null>(null);
  const [players, setPlayers] = useState<Player[]>([]);
  const [gameState, setGameState] = useState<GameState>('waiting');
  const [currentPart, setCurrentPart] = useState<GamePart | null>(null);
  const [nextPart, setNextPart] = useState<GamePart | null>(null);
  const [pickOrderState, setPickOrderState] = useState<{
    sortedPlayerIds: string[];
    currentTurnIndex: number;
    takenPositions: Record<number, string>;
  } | undefined>(undefined);
  const [selectedAnswer, setSelectedAnswer] = useState<number | string | number[] | null>(null);
  const [isAdmin, setIsAdmin] = useState<boolean>(false);
  const [timer, setTimer] = useState<number | null>(null);
  const [recentAnswers, setRecentAnswers] = useState<Array<{ playerId: string; timestamp: number }>>([]);
  const [currentPartIndex, setCurrentPartIndex] = useState<number>(-1);
  const [totalParts, setTotalParts] = useState<number>(0);

  // Store refs for matching in callbacks
  const playerNameRef = useRef<string>('');
  const playerIdRef = useRef<string | null>(null);

  // Initialize socket connection
  useEffect(() => {
    const newSocket = io(WS_URL, {
      transports: ['websocket', 'polling'],
    });

    newSocket.on('connect', () => {
      console.log('Connected to server');
      setIsConnected(true);

      // Attempt to reconnect if session exists
      const savedSession = localStorage.getItem('game_session');
      if (savedSession) {
        const { roomCode, playerId, playerName, role } = JSON.parse(savedSession);
        console.log('Found saved session, attempting to reconnect:', { roomCode, playerName });

        // Set refs before emitting to ensure event handlers work correctly
        playerNameRef.current = playerName;

        // We need to emit join with the saved playerId
        newSocket.emit('room:join', { roomCode, playerName, playerId }, (response: any) => {
          if (response.success) {
            console.log('Reconnected successfully');
            setRoomCode(roomCode);
            setPlayerName(playerName);
            setPlayerId(playerId);
            playerIdRef.current = playerId;
            setRole(role);
          } else {
            console.warn('Failed to reconnect:', response.error);
            localStorage.removeItem('game_session');
          }
        });
      }
    });

    newSocket.on('disconnect', () => {
      console.log('Disconnected from server');
      setIsConnected(false);
    });

    // Room events
    newSocket.on('room:joined', (data) => {
      console.log('Room joined:', data);
      setRoomCode(data.roomCode);
      setPlayers(data.players);
      setGameState(data.gameState);
      setCurrentPart(data.currentPart);
      setNextPart(data.nextPart);
      setTimer(data.timer);
      setPickOrderState(data.pickOrderState);
      setCurrentPartIndex(data.currentPartIndex);
      setTotalParts(data.totalParts);

      // Find current player by matching name
      const currentPlayer = data.players.find((p: Player) =>
        p.name === playerNameRef.current
      );
      if (currentPlayer) {
        setPlayerId(currentPlayer.id);
        playerIdRef.current = currentPlayer.id;
        setPlayerName(currentPlayer.name);
        setIsAdmin(currentPlayer.role === 'admin');
        // Restore selected answer if any
        if (currentPlayer.currentAnswer !== null) {
          setSelectedAnswer(currentPlayer.currentAnswer);
        }
      }
    });

    newSocket.on('room:player-joined', (data) => {
      console.log('Player joined:', data);
      setPlayers(data.players);
    });

    newSocket.on('room:player-left', (data) => {
      console.log('Player left:', data);
      setPlayers(data.players);
    });

    newSocket.on('room:error', (data) => {
      setTimer(data.timer);
    });

    newSocket.on('game:results', (data) => {
      console.log('Results:', data);
      // We can update local state if needed, but state-changed usually follows or handles UI
    });

    newSocket.on('game:answer-received', (data) => {
      console.log('Answer received:', data);
      // Add to recent answers for notification display
      const notification = { playerId: data.playerId, timestamp: Date.now() };
      setRecentAnswers((prev) => [...prev, notification]);

      // Auto-remove after 2.5 seconds (animation duration + buffer)
      setTimeout(() => {
        setRecentAnswers((prev) => prev.filter((n) => n.timestamp !== notification.timestamp));
      }, 2500);
    });

    // Game state changes
    newSocket.on('game:state-changed', (data) => {
      console.log('game:state-changed received:', JSON.stringify(data, null, 2));
      console.log('Updating state to:', data.state);
      setGameState(data.state);
      setCurrentPart(data.part);
      setNextPart(data.nextPart);
      setTimer(data.timer);
      setPlayers(data.players);
      setPickOrderState(data.pickOrderState);
      setCurrentPartIndex(data.currentPartIndex);
      setTotalParts(data.totalParts);

      // Reset selected answer when moving to a new question
      if (data.state === 'question-intro' ||
        data.state === 'color-question-intro' ||
        data.state === 'majority-question-intro' ||
        data.state === 'number-picker-intro' ||
        data.state === 'multi-select-intro' ||
        data.state === 'pachinko-intro' ||
        data.state === 'survey-poll-intro' ||
        data.state === 'survey-guess-intro') {
        setSelectedAnswer(null);
      }
    });

    newSocket.on('game:timer-update', (data) => {
      console.log('Timer update:', data.timer);
      setTimer(data.timer);
    });

    // Admin events
    newSocket.on('admin:logged-in', () => {
      console.log('Admin logged in');
      setIsAdmin(true);
      setRole('admin');
      // Update player role in list
      setPlayers((prev) =>
        prev.map((p) => (p.id === playerIdRef.current ? { ...p, role: 'admin' } : p))
      );
    });

    setSocket(newSocket);

    return () => {
      newSocket.close();
    };
  }, []); // Only run once on mount

  // Save session to localStorage
  useEffect(() => {
    if (roomCode && playerId && playerName) {
      localStorage.setItem('game_session', JSON.stringify({ roomCode, playerId, playerName, role }));
    }
  }, [roomCode, playerId, playerName, role]);

  useEffect(() => {
    playerNameRef.current = playerName;
  }, [playerName]);

  useEffect(() => {
    playerIdRef.current = playerId;
  }, [playerId]);

  const createRoom = () => {
    if (!socket) return;
    socket.emit('room:create', (roomCode: string) => {
      console.log('Room created:', roomCode);
      setRoomCode(roomCode);
      setRole('host');
      setPlayers([]);
    });
  };

  const joinRoom = (roomCode: string, name: string, broughtGift: boolean) => {
    if (!socket) return;
    playerNameRef.current = name;
    setPlayerName(name);
    // @ts-ignore
    socket.emit('room:join', { roomCode, playerName: name, playerId: playerId || undefined, broughtGift }, (response) => {
      if (response.success) {
        console.log('Successfully joined room:', response.roomCode);
        setRole('client');
      } else {
        console.error('Failed to join room:', response.error);
        alert(response.error || 'Failed to join room');
      }
    });
  };

  const leaveRoom = () => {
    if (!socket || !roomCode) return;
    socket.emit('room:leave');
    setRoomCode(null);
    setRole(null);
    setPlayers([]);
    setGameState('waiting');
    setCurrentPart(null);
    setNextPart(null);
    setSelectedAnswer(null);
    setIsAdmin(false);
    setTimer(null);
    localStorage.removeItem('game_session');
  };

  const submitAnswer = (answerIndex: number | string | number[]) => {
    if (!socket) return;
    socket.emit('game:submit-answer', { answerIndex });
    setSelectedAnswer(answerIndex);
  };

  const submitDraft = (answerIndex: number | string | number[]) => {
    if (!socket) return;
    socket.emit('game:submit-draft', { answerIndex });
  };

  const adminLogin = async (password: string): Promise<boolean> => {
    if (!socket) return false;
    return new Promise((resolve) => {
      // @ts-ignore
      socket.emit('admin:login', { password }, (response) => {
        if (response.success) {
          resolve(true);
        } else {
          alert(response.error || 'Invalid password');
          resolve(false);
        }
      });
    });
  };

  const resetTimer = (seconds: number) => {
    if (!socket) return;
    socket.emit('admin:reset-timer', { seconds });
  };

  const advanceGame = () => {
    if (!socket) {
      console.log('advanceGame: No socket connection');
      return;
    }
    console.log('advanceGame: Emitting game:advance event');
    socket.emit('game:advance');
  };

  const submitPachinkoResults = (results: { playerId: string; bucketIndex: number }[]) => {
    if (!socket) return;
    socket.emit('game:submit-pachinko-results', { results });
  };

  const submitWager = (wager: number) => {
    if (!socket) return;
    socket.emit('game:submit-wager', { wager });
  };

  const value: GameContextType = {
    socket,
    isConnected,
    roomCode,
    setRoomCode,
    role,
    setRole,
    playerName,
    setPlayerName,
    playerId,
    setPlayerId,
    players,
    gameState,
    currentPart,
    nextPart,
    currentPartIndex,
    totalParts,
    pickOrderState,
    selectedAnswer,
    setSelectedAnswer,
    isAdmin,
    setIsAdmin,
    timer,
    recentAnswers,
    createRoom,
    joinRoom,
    leaveRoom,
    submitAnswer,
    adminLogin,
    resetTimer,
    advanceGame,
    submitPachinkoResults,
    submitDraft,
    submitWager
  };

  return <GameContext.Provider value={value}>{children}</GameContext.Provider>;
};
