import React from 'react';
import { Question as QuestionType, GameState } from '../types';
import { useGame } from '../contexts/GameContext';
import TimerDisplay from './TimerDisplay';

interface QuestionDisplayProps {
    data: QuestionType;
    state: GameState;
}

const HostResultsList: React.FC<{ correctAnswer: number; answers: string[] }> = ({ correctAnswer, answers }) => {
    const { players } = useGame();

    return (
        <>
            {players.filter(player => player.role !== 'admin').map(player => {
                const isCorrect = player.currentAnswer === correctAnswer;
                const hasAnswered = player.currentAnswer !== null && player.currentAnswer !== undefined;

                return (
                    <div key={player.id} className={`player-result-card ${isCorrect ? 'correct' : 'incorrect'}`}
                        style={{
                            background: "rgba(34, 34, 34, 0.2)",
                            padding: '10px',
                            borderRadius: '8px',
                            border: `1px solid ${isCorrect ? '#c3e6cb' : '#f5c6cb'}`,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between'
                        }}>
                        <span style={{ fontWeight: 'bold' }}>{player.name}</span>
                        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                            {hasAnswered ? (
                                <>
                                    <span style={{ fontSize: '0.9em' }}>
                                        {typeof player.currentAnswer === 'number' ? answers[player.currentAnswer] : '-'}
                                    </span>
                                    <span style={{ fontSize: '1.2em' }}>
                                        {isCorrect ? '' : ''}
                                    </span>
                                </>
                            ) : (
                                <span style={{ fontStyle: 'italic', color: '#ffffffff' }}>No Answer</span>
                            )}
                        </div>
                    </div>
                );
            })}
        </>
    );
};

const QuestionDisplay: React.FC<QuestionDisplayProps> = ({ data, state }) => {
    const { submitAnswer, selectedAnswer, role } = useGame();
    const isClient = role === 'client';

    const handleAnswerClick = (index: number) => {
        if (isClient && state === 'question-timer' && selectedAnswer === null) {
            submitAnswer(index);
        }
    };

    return (
        <div className="question-display">
            <div className="question-header">
                <h2 className="question-text">{data.text}</h2>
                <div style={{ fontSize: '1.5rem', display: 'flex', flexDirection: 'column', alignItems: 'center' }} className="question-meta">
                    <span><i>Earn Up to <b>{Math.floor(data.points * 1.5)}</b> Points</i></span>
                    <span style={{ fontSize: '0.6em', opacity: 0.8 }}>({data.points} base + up to {Math.floor(data.points * 0.5)} speed bonus)</span>
                    <TimerDisplay totalTime={data.timeLimit} />
                </div>
            </div>

            {(state === 'question-timer' || (state === 'question-results' && isClient)) && (
                <div className="answers-grid">
                    {data.answers.map((answer, index) => {
                        let className = 'answer-button';

                        if (state === 'question-results' && isClient) {
                            if (index === data.correctAnswer) className += ' correct';
                            else if (selectedAnswer === index) className += ' incorrect';
                        } else if (selectedAnswer === index) {
                            className += ' selected';
                        }

                        if (state !== 'question-timer' && isClient) {
                            className += ' disabled';
                        }

                        return (
                            <button
                                key={index}
                                className={className}
                                onClick={() => handleAnswerClick(index)}
                                disabled={!isClient || state !== 'question-timer' || selectedAnswer !== null}
                            >
                                {answer}
                            </button>
                        );
                    })}
                </div>
            )}

            {/* Host Results View */}
            {state === 'question-results' && !isClient && (
                <div className="host-results-list" style={{ marginTop: '2rem', display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))', gap: '1rem' }}>
                    {/* We need players from context to show this list. Import useGame hook at top is already there, check if we're pulling players. */}
                    {/* The current implementation doesn't destructure players from useGame within QuestionDisplay, need to add it. */}
                    <HostResultsList correctAnswer={data.correctAnswer} answers={data.answers} />
                </div>
            )}

            {state === 'question-results' && isClient && (
                <div className="results-message">
                    {selectedAnswer === data.correctAnswer ? (
                        <h3 className="correct-msg">Correct!</h3>
                    ) : (
                        <h3 className="incorrect-msg">Incorrect</h3>
                    )}
                </div>
            )}
        </div>
    );
};

// Helper component for Host Results
export default QuestionDisplay;
