import React, { useMemo, useState, useEffect, useRef } from 'react';
import { NumberPickerQuestion, Player } from '../types';

interface NumberResultsProps {
    data: NumberPickerQuestion;
    players: Player[];
    showCorrectAnswer?: boolean;
}

const NumberResults: React.FC<NumberResultsProps> = ({ data, players, showCorrectAnswer = false }) => {
    const { minValue, maxValue, correctAnswer, points } = data;
    const range = maxValue - minValue;

    const [visibleAnswers, setVisibleAnswers] = useState<number[]>([]);
    const hasAnimated = useRef(false);

    const getPercentage = (value: number) => {
        return ((value - minValue) / range) * 100;
    };

    // Group players by answer to avoid stacking overlap if same number chosen
    const groupedAnswers = useMemo(() => {
        const groups = new Map<number, Player[]>();
        players.filter(p => p.role !== 'admin').forEach(p => {
            if (typeof p.currentAnswer === 'number') {
                const list = groups.get(p.currentAnswer) || [];
                list.push(p);
                groups.set(p.currentAnswer, list);
            }
        });
        return groups;
    }, [players]);

    // Unique answers sorted for animation order (min to max)
    const sortedUniqueAnswers = useMemo(() => {
        const sorted = Array.from(groupedAnswers.keys()).sort((a, b) => a - b);
        console.log('[NumberResults] sortedUniqueAnswers:', sorted);
        console.log('[NumberResults] groupedAnswers keys:', Array.from(groupedAnswers.keys()));
        return sorted;
    }, [groupedAnswers]);

    // Animation Effect
    useEffect(() => {
        console.log('[NumberResults] Animation effect triggered. sortedUniqueAnswers:', sortedUniqueAnswers, 'hasAnimated:', hasAnimated.current);

        // Only run animation if it hasn't run yet
        if (hasAnimated.current) {
            console.log('[NumberResults] Animation already ran, skipping');
            return;
        }

        // Reset state when new data arrives
        setVisibleAnswers([]);

        let interval: NodeJS.Timeout | null = null;

        // Use setTimeout to ensure state reset completes before starting animation
        const startTimeout = setTimeout(() => {
            let currentIndex = 0;

            interval = setInterval(() => {
                console.log('=== Interval tick ===');
                console.log('sortedUniqueAnswers:', sortedUniqueAnswers);
                console.log('currentIndex:', currentIndex);
                console.log('sortedUniqueAnswers.length:', sortedUniqueAnswers.length);

                if (currentIndex < sortedUniqueAnswers.length) {
                    // Reveal next player answer
                    const valueToAdd = sortedUniqueAnswers[currentIndex];
                    console.log("Value to add:", valueToAdd, "Type:", typeof valueToAdd);
                    console.log("showing player answer", valueToAdd);

                    setVisibleAnswers(prev => {
                        console.log('Previous visibleAnswers:', prev);
                        const newArray = [...prev, valueToAdd];
                        console.log('New visibleAnswers:', newArray);
                        return newArray;
                    });
                    currentIndex++;
                } else {
                    // All player answers shown, stop the interval
                    hasAnimated.current = true;
                    if (interval) clearInterval(interval);
                }
            }, 800); // 800ms delay between each reveal
        }, 50); // Small delay to ensure state reset

        return () => {
            clearTimeout(startTimeout);
            if (interval) clearInterval(interval);
        };
    }, [sortedUniqueAnswers]);

    return (
        <div className="results-container number-results" style={{ width: '80vw', margin: '0 auto', padding: '2rem 0' }}>
            {/* <h3>Final Results</h3>
            <p>Correct Answer: <strong>{correctAnswer}</strong></p> */}

            <div className="number-line-container" style={{ fontWeight: 700, position: 'relative', height: '150px', marginTop: '4rem', marginBottom: '2rem', borderBottom: '4px solid #ffffffff' }}>

                {/* Ticks */}
                <div style={{ fontSize: "2rem", position: 'absolute', left: '0%', bottom: '-50px' }}>{minValue}</div>
                <div style={{ fontSize: "2rem", position: 'absolute', left: '100%', bottom: '-50px', transform: 'translateX(-100%)' }}>{maxValue}</div>

                {/* Correct Answer Flag */}
                <div
                    className="correct-flag"
                    style={{
                        position: 'absolute',
                        left: `${getPercentage(correctAnswer)}%`,
                        bottom: '0',
                        transform: 'translateX(-50%)',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        zIndex: 10,
                        opacity: showCorrectAnswer ? 1 : 0,
                        transition: 'opacity 0.5s ease-in-out'
                    }}
                >
                    <div style={{ background: '#2ecc71', color: 'white', padding: '4px 8px', borderRadius: '4px', marginBottom: '5px', whiteSpace: 'nowrap' }}>
                        Answer: {correctAnswer}
                    </div>
                    <div style={{ width: '4px', height: '30px', background: '#2ecc71' }}></div>
                </div>

                {/* Player Answers */}
                {Array.from(groupedAnswers.entries()).map(([answer, playerList]) => {
                    const pct = getPercentage(answer);
                    const isVisible = visibleAnswers.includes(answer);

                    console.log(`Rendering answer ${answer}, isVisible: ${isVisible}, visibleAnswers:`, visibleAnswers, 'playerList:', playerList.map(p => p.name));

                    return (
                        <div
                            key={answer}
                            className="player-marker-group"
                            style={{
                                position: 'absolute',
                                left: `${pct}%`,
                                bottom: '10px',
                                transform: 'translateX(-50%)',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                opacity: isVisible ? 1 : 0,
                                transition: 'opacity 0.5s ease-in-out'
                            }}
                        >
                            <div className="player-names" style={{
                                background: 'rgba(33,33,33,0.2)',
                                padding: '5px',
                                borderRadius: '5px',
                                border: '1px solid #ccc',
                                fontSize: '0.8rem',
                                textAlign: 'center',
                                boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
                            }}>
                                <div style={{ fontWeight: 'bold', color: '#ffffffff' }}>{answer}</div>
                                {playerList.map(p => (
                                    <div key={p.id}>
                                        {p.name}
                                    </div>
                                ))}
                            </div>

                            <div style={{
                                width: '12px',
                                height: '12px',
                                background: '#e74c3c',
                                borderRadius: '50%',
                                marginTop: '5px'
                            }}></div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default NumberResults;
