import React from 'react';
import { useGame } from '../contexts/GameContext';
import './AdminControls.css';

const AdminControls: React.FC = () => {
  const {
    gameState,
    timer,
    resetTimer,
    advanceGame,
  } = useGame();

  const handleAdvance = () => {
    advanceGame();
  };

  const handleResetTimer = (seconds: number) => {
    resetTimer(seconds);
  };

  return (
    <div className="admin-controls">
      <div className="admin-header">
        <span className="admin-badge"> Admin Controls</span>
      </div>
      <div className="admin-buttons">
        <button className="admin-button" onClick={handleAdvance}>
          Advance Game State
        </button>

        <div className="timer-controls">
          <span className="timer-label">Timer:</span>
          <button className="admin-button timer-button" onClick={() => handleResetTimer(10)}>
            10s
          </button>
          <button className="admin-button timer-button" onClick={() => handleResetTimer(30)}>
            30s
          </button>
          <button className="admin-button timer-button" onClick={() => handleResetTimer(60)}>
            60s
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminControls;
