import React, { useState } from 'react';
import { useGame } from '../contexts/GameContext';
import './WagerDisplay.css';

interface WagerDisplayProps {
    // Add props if needed, but context is usually enough
}

export const WagerDisplay: React.FC<WagerDisplayProps> = () => {
    const { gameState, currentPart, playerId, submitWager, players, role } = useGame();
    const [wagerAmount, setWagerAmount] = useState<number>(0);
    const [hasSubmitted, setHasSubmitted] = useState<boolean>(false);

    const player = players.find(p => p.id === playerId);

    // Allow host to view even if not a player (host role handling)
    if (role === 'host') {
        const wageredCount = players.filter(p => p.wager !== undefined).length;
        const totalCount = players.filter(p => p.role !== 'admin').length;

        return (
            <div className="wager-display host-view" style={{ background: "rgba(0,0,0,0)" }}>
                <h1>Final Question Wager</h1>
                <div className="wager-status">
                    <h3>Get in the top half to receive double your wager! Land in the bottom half and lose your wager...</h3>
                    <div className="wager-count">
                        {wageredCount} / {totalCount} Players Ready
                    </div>
                </div>
                <div className="player-list-wager">
                    {players.filter(p => p.role !== 'admin').map(p => (
                        <div key={p.id} className={`player-wager-item ${p.wager !== undefined ? 'ready' : ''}`}>
                            {p.name} {p.wager !== undefined ? '' : '...'}
                        </div>
                    ))}
                </div>
            </div>
        );
    }

    if (!player) return <div>Loading player data...</div>;

    const maxWager = player.score;

    const handleSubmit = () => {
        if (wagerAmount < 0 || wagerAmount > maxWager) return;
        submitWager(wagerAmount);
        setHasSubmitted(true);
    };

    // For Client View
    return (
        <div className="wager-display client-view" style={{ background: "rgba(0,0,0,0)" }}>
            <h1>Final Wager</h1>
            <p>How much do you want to wager?</p>

            {!hasSubmitted ? (
                <div className="wager-input-container">
                    <input
                        type="range"
                        min="0"
                        max={maxWager}
                        value={wagerAmount}
                        onChange={(e) => setWagerAmount(parseInt(e.target.value))}
                        className="wager-slider"
                        style={{ accentColor: "#ebe721ff" }}
                    />
                    <div className="wager-amount-display">{wagerAmount}</div>

                    <div className="wager-presets">
                        <button onClick={() => setWagerAmount(0)}>0</button>
                        <button onClick={() => setWagerAmount(Math.floor(maxWager / 2))}>50%</button>
                        <button onClick={() => setWagerAmount(maxWager)}>All In!</button>
                    </div>

                    <button className="submit-wager-btn" onClick={handleSubmit}>
                        Place Wager
                    </button>
                </div>
            ) : (
                <div className="wager-submitted">
                    <h2>Good Luck!</h2>
                    <i>You wagered {wagerAmount} points</i>
                </div>
            )}
        </div>
    );
};
