import React from 'react';
import { useNavigate } from 'react-router-dom';
import './LandingPage.css';

const LandingPage: React.FC = () => {
  const navigate = useNavigate();

  return (
    <div className="landing-page">
      <div className="landing-container">
        <h1 className="landing-title">SOCFI 2025 White Elephant</h1>
        <p className="landing-subtitle">Join or host a game session</p>

        <div className="landing-buttons">
          <button
            className="landing-button host-button"
            onClick={() => navigate('/host')}
          >
            <span className="button-text">
              <strong>Host Game</strong>
              <small>Don't click this button unless you are abandoning Andrew and running your own White Elephant event (he WILL be sad if you leave him).</small>
            </span>
          </button>

          <button
            className="landing-button client-button"
            onClick={() => navigate('/client')}
          >
            <span className="button-text">
              <strong>Join Game</strong>
              <small>yeah click this button yeah yeah</small>
            </span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;

