import React, { useMemo } from 'react';
import { Standing as StandingType, Player } from '../types';
import './Standing.css';

interface StandingProps {
    data: StandingType;
    players: Player[];
    currentUserId?: string; // Optional: if present, only show this user's standing
}

const Standing: React.FC<StandingProps> = ({ data, players, currentUserId }) => {
    // GENERATE DUMMY DATA FOR TESTING
    const allPlayers = useMemo(() => {
        // const dummyPlayers = Array.from({ length: 30 }, (_, i) => ({
        //     id: `dummy-${i}`,
        //     name: `Test Player ${i + 1}`,
        //     score: Math.floor(Math.random() * 2000), // Random score up to 2000
        //     role: 'client' as const,
        //     currentAnswer: null,
        //     socketId: `dummy-socket-${i}`,
        //     isConnected: true,
        //     lastSeen: new Date()
        // }));
        const dummyPlayers: Player[] = [];
        return [...players, ...dummyPlayers];
    }, [players]);

    const sortedPlayers = useMemo(() => {
        return [...allPlayers].filter(player => player.role !== 'admin').sort((a, b) => b.score - a.score);
    }, [allPlayers]);

    return (
        <div className={`standing-screen ${currentUserId ? 'client-view' : ''}`}>
            <h1>{data.title || 'Final Results'}</h1>
            <div className="standings-list-container">
                {sortedPlayers.map((player, index) => {
                    // If currentUserId is provided, only show that player
                    if (currentUserId && player.id !== currentUserId) {
                        return null;
                    }

                    return (
                        <div key={player.id} className="standing-item">
                            <div className="rank">#{index + 1}</div>
                            <div className="player-name">{player.name}</div>
                            <div className="player-score">{player.score}</div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default Standing;
