import React, { useState } from 'react';
import { PachinkoQuestion, GameState } from '../types';
import { useGame } from '../contexts/GameContext';
import './PachinkoSelector.css';

interface PachinkoSelectorProps {
    data: PachinkoQuestion;
    state: GameState;
}

const PachinkoSelector: React.FC<PachinkoSelectorProps> = ({ data, state }) => {
    const { submitAnswer } = useGame();
    const [selectedSlot, setSelectedSlot] = useState<number | null>(null);
    const [hasSubmitted, setHasSubmitted] = useState(false);

    const handleSlotSelect = (slotIndex: number) => {
        if (hasSubmitted || state !== 'pachinko-timer') return;

        setSelectedSlot(slotIndex);
        submitAnswer(slotIndex);
        setHasSubmitted(true);
    };

    if (state === 'pachinko-intro') {
        return (
            <div className="pachinko-selector">
                <h2 className="pachinko-question">{data.text}</h2>
                <p className="pachinko-instruction">Get ready to select your drop slot!</p>
            </div>
        );
    }

    if (state === 'pachinko-timer') {
        return (
            <div className="pachinko-selector">
                <h2 className="pachinko-question">{data.text}</h2>
                <p className="pachinko-instruction">Select a slot to drop your circle:</p>

                <div className="slot-grid">
                    {Array.from({ length: data.numSlots }, (_, i) => (
                        <button
                            key={i}
                            className={`slot-button ${selectedSlot === i ? 'selected' : ''} ${hasSubmitted ? 'disabled' : ''}`}
                            onClick={() => handleSlotSelect(i)}
                            disabled={hasSubmitted}
                        >
                            {i + 1}
                        </button>
                    ))}
                </div>

                {hasSubmitted && (
                    <div className="submission-confirmation">
                        <p> Slot {(selectedSlot ?? 0) + 1} selected!</p>
                        <p className="waiting-text">Waiting for other players...</p>
                    </div>
                )}
            </div>
        );
    }

    if (state === 'pachinko-results') {
        return (
            <div className="pachinko-selector">
                <h2 className="pachinko-question">Watch the circles drop!</h2>
                <p className="pachinko-instruction">Check the main screen to see where your circle lands!</p>
                {selectedSlot !== null && (
                    <div className="your-selection">
                        <p>Your slot: <strong>{selectedSlot + 1}</strong></p>
                    </div>
                )}
            </div>
        );
    }

    return null;
};

export default PachinkoSelector;
