import React, { useState, useEffect } from 'react';
import { NumberPickerQuestion as NumberPickerQuestionType, GameState } from '../types';
import { useGame } from '../contexts/GameContext';
import './NumberPickerDisplay.css';
import NumberResults from './NumberResults';
import TimerDisplay from './TimerDisplay';

interface NumberPickerDisplayProps {
    data: NumberPickerQuestionType;
    state: GameState;
}

const NumberPickerDisplay: React.FC<NumberPickerDisplayProps> = ({ data, state }) => {
    const { submitAnswer, submitDraft, selectedAnswer, timer, role, players } = useGame();
    const isClient = role === 'client';
    const step = data.step || 1;

    const [sliderValue, setSliderValue] = useState<number>(Math.floor((data.minValue + data.maxValue) / 2));
    const [inputValue, setInputValue] = useState<string>(sliderValue.toString());

    useEffect(() => {
        console.log('NumberPickerDisplay - state:', state, 'isClient:', isClient, 'selectedAnswer:', selectedAnswer);
    }, [state, isClient, selectedAnswer]);

    // Submit draft answer whenever slider value changes
    useEffect(() => {
        if (isClient && state === 'number-picker-timer' && selectedAnswer === null) {
            submitDraft(sliderValue);
        }
    }, [sliderValue, isClient, state, selectedAnswer, submitDraft]);

    const handleSliderChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = parseInt(e.target.value);
        setSliderValue(value);
        setInputValue(value.toString());
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputValue(e.target.value);
    };

    const handleInputBlur = () => {
        let value = parseInt(inputValue);

        // Clamp to range if out of bounds
        if (isNaN(value)) {
            value = sliderValue;
        } else if (value < data.minValue) {
            value = data.minValue;
        } else if (value > data.maxValue) {
            value = data.maxValue;
        }

        setSliderValue(value);
        setInputValue(value.toString());
    };

    const handleSubmit = () => {
        if (isClient && state === 'number-picker-timer' && (selectedAnswer === null || selectedAnswer === undefined)) {
            submitAnswer(sliderValue);
        }
    };

    const getAccuracyPercentage = (): number => {
        if (typeof selectedAnswer !== 'number') return 0;
        const range = data.maxValue - data.minValue;
        const distance = Math.abs(selectedAnswer - data.correctAnswer);
        return Math.round((1 - (distance / range)) * 100);
    };

    const showControls = isClient && (selectedAnswer === null || selectedAnswer === undefined) && (state === 'number-picker-intro' || state === 'number-picker-timer');

    return (
        <div style={{ background: 'rgba(0, 0, 0, 0)' }} className={`number-picker-display ${!isClient ? 'host-view' : ''}`}>
            <div className="number-picker-header">
                <h2 className="number-picker-text">{data.text}</h2>
                <div className="number-picker-meta">
                    <i>
                        {data.isFinal !== true && (
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginRight: '20px' }}>
                                <span>Earn Up to <b>{Math.floor(data.points * 1.5)}</b> Points</span>
                                <span style={{ fontSize: '0.6em', opacity: 0.8 }}>({data.points} base + up to {Math.floor(data.points * 0.5)} speed bonus)</span>
                            </div>
                        )}
                        <span>Range: {data.minValue} to {data.maxValue}</span>
                    </i>
                </div>
            </div>

            {state === 'number-picker-timer' && (
                <TimerDisplay totalTime={data.timeLimit} />
            )}

            {showControls && (
                <div className="number-picker-controls">
                    <div className="current-value-display">
                        <h3>Your Answer: {sliderValue}</h3>
                    </div>

                    <div className="slider-container">
                        <span className="range-label">{data.minValue}</span>
                        <input
                            type="range"
                            min={data.minValue}
                            max={data.maxValue}
                            step={step}
                            value={sliderValue}
                            onChange={handleSliderChange}
                            className="number-slider"
                        />
                        <span className="range-label">{data.maxValue}</span>
                    </div>

                    <div className="input-container">
                        <label htmlFor="number-input">Or type a number:</label>
                        <input
                            id="number-input"
                            type="number"
                            min={data.minValue}
                            max={data.maxValue}
                            step={step}
                            value={inputValue}
                            onChange={handleInputChange}
                            onBlur={handleInputBlur}
                            className="number-input"
                        />
                    </div>

                    {state === 'number-picker-timer' && (
                        <button
                            className="submit-number-button"
                            onClick={handleSubmit}
                        >
                            Submit Answer
                        </button>
                    )}
                </div>
            )}

            {isClient && selectedAnswer !== null && selectedAnswer !== undefined && (
                <div className="number-picker-results">
                    <div className="answer-comparison">
                        <div className="answer-box">
                            <h4>Your Answer</h4>
                            <p className="answer-value">{selectedAnswer}</p>
                        </div>
                        {state === 'number-picker-results-reveal' && (
                            <div className="answer-box correct">
                                <h4>Correct Answer</h4>
                                <p className="answer-value">{data.correctAnswer}</p>
                            </div>
                        )}
                    </div>
                    {state === 'number-picker-results-reveal' && (
                        <div className="accuracy-display">
                            <p>Accuracy: {getAccuracyPercentage()}%</p>
                        </div>
                    )}
                </div>
            )}

            {!isClient && (state === 'number-picker-results' || state === 'number-picker-results-reveal') && (
                <NumberResults
                    data={data}
                    players={players}
                    showCorrectAnswer={state === 'number-picker-results-reveal'}
                />
            )}
        </div>
    );
};

export default NumberPickerDisplay;
