// Toggle this to switch between development and production
export const DEV_MODE = false;

// WebSocket server URLs (Socket.IO format - no protocol prefix needed, just host:port or full URL)
export const WS_URL_DEV = 'http://localhost:3001';
export const WS_URL_PROD = 'https://oracle.andrewtong.dev';

// Get the appropriate WebSocket URL based on dev mode
export const WS_URL = DEV_MODE ? WS_URL_DEV : WS_URL_PROD;

// API endpoints (if needed for REST calls)
export const API_URL_DEV = 'http://localhost:3001';
export const API_URL_PROD = 'https://oracle.andrewtong.dev';

export const API_URL = DEV_MODE ? API_URL_DEV : API_URL_PROD;

