import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../contexts/GameContext';
import AdminControls from './AdminControls';
import TitleCard from './TitleCard';
import ImageDisplay from './ImageDisplay';
import QuestionDisplay from './QuestionDisplay';
import ColorQuestionDisplay from './ColorQuestionDisplay';
import MajorityQuestionDisplay from './MajorityQuestionDisplay';
import NumberPickerDisplay from './NumberPickerDisplay';
import MultiSelectDisplay from './MultiSelectDisplay';
import PachinkoSelector from './PachinkoSelector';
import Standing from './Standing';
import { PickOrderDisplay } from './PickOrderDisplay';
import { WagerDisplay } from './WagerDisplay';
import SurveyDisplay from './SurveyDisplay';
import './ClientView.css';

const ClientView: React.FC = () => {
  const navigate = useNavigate();
  const {
    roomCode,
    playerName,
    playerId,
    gameState,
    currentPart,
    isAdmin,
    players,
    joinRoom,
    leaveRoom,
    adminLogin,
  } = useGame();

  const [roomCodeInput, setRoomCodeInput] = useState('');
  const [nameInput, setNameInput] = useState('');
  const [passwordInput, setPasswordInput] = useState('');
  const [showPasswordInput, setShowPasswordInput] = useState(false);
  const [showLeaveConfirm, setShowLeaveConfirm] = useState(false);
  const [broughtGift, setBroughtGift] = useState(true);
  const [isJoined, setIsJoined] = useState(false);

  const handleJoinRoom = () => {
    if (roomCodeInput.trim() && nameInput.trim()) {
      const code = roomCodeInput.toUpperCase();
      const name = nameInput.trim();
      setIsJoined(true); // Optimistically set to true
      joinRoom(code, name, broughtGift);
    }
  };

  const handleLeaveGame = () => {
    leaveRoom();
    setShowLeaveConfirm(false);
    setIsJoined(false);
  };

  // Reset isJoined if join failed (no roomCode after a delay)
  useEffect(() => {
    if (isJoined && !roomCode) {
      const timer = setTimeout(() => {
        if (!roomCode) {
          setIsJoined(false);
        }
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [isJoined, roomCode]);

  const handleAdminLogin = async () => {
    const success = await adminLogin(passwordInput);
    if (success) {
      setShowPasswordInput(false);
      setPasswordInput('');
    }
  };

  // Check if we're already in a room (from previous session or navigation)
  useEffect(() => {
    if (roomCode && playerName) {
      setIsJoined(true);
    }
  }, [roomCode, playerName]);

  if (!isJoined) {
    return (
      <div className="client-view">
        <div className="client-container">
          <h1>Join a Game</h1>
          <div className="join-form">
            <div className="form-group">
              <label htmlFor="roomCode">Room Code</label>
              <input
                id="roomCode"
                type="text"
                value={roomCodeInput}
                onChange={(e) => setRoomCodeInput(e.target.value.toUpperCase())}
                placeholder="Enter room code"
                maxLength={6}
                className="form-input"
              />
            </div>
            <div className="form-group">
              <label htmlFor="playerName">Your Name</label>
              <input
                id="playerName"
                type="text"
                value={nameInput}
                onChange={(e) => setNameInput(e.target.value)}
                placeholder="Enter your name"
                className="form-input"
              />
            </div>
            <div className="form-group checkbox-group">
              <label className="checkbox-label">
                <input
                  style={{ verticalAlign: 'middle' }}
                  type="checkbox"
                  checked={broughtGift}
                  onChange={(e) => setBroughtGift(e.target.checked)}
                  className="form-checkbox"
                />
                <span style={{ marginLeft: '10px', verticalAlign: 'middle' }}>
                  I brought a gift!
                </span>
              </label>
            </div>
            <button
              className="join-button"
              onClick={handleJoinRoom}
              disabled={!roomCodeInput.trim() || !nameInput.trim()}
            >
              Join Room
            </button>
            <button
              className="back-button"
              onClick={() => navigate('/')}
            >
              Back to Home
            </button>
          </div>
        </div>
      </div>
    );
  }

  const renderContent = () => {
    if (gameState === 'waiting') {
      return (
        <div className="waiting-screen">
          <h2>Waiting for game to start...</h2>
          <p>Keep this page open and wait for the host to begin</p>
        </div>
      );
    }

    if (gameState === 'wager') {
      return <WagerDisplay />;
    }

    if (!currentPart) return <div>Loading...</div>;

    switch (currentPart.type) {
      case 'titlecard':
        return <TitleCard data={currentPart} />;
      case 'image-display':
        return <ImageDisplay data={currentPart} />;
      case 'question':
        return <QuestionDisplay data={currentPart} state={gameState} />;
      case 'color-question':
        return <ColorQuestionDisplay data={currentPart} state={gameState} />;
      case 'majority-question':
        return <MajorityQuestionDisplay data={currentPart} state={gameState} />;
      case 'number-picker-question':
        return <NumberPickerDisplay data={currentPart} state={gameState} />;
      case 'multi-select-question':
        return <MultiSelectDisplay data={currentPart} state={gameState} />;
      case 'pachinko-question':
        return <PachinkoSelector data={currentPart} state={gameState} />;
      case 'standing':
        return <Standing data={currentPart} players={players} currentUserId={playerId || undefined} />;
      case 'pick-order':
        return <PickOrderDisplay isHost={false} />;
      case 'survey-question':
        return <SurveyDisplay data={currentPart} state={gameState} />;
      default:
        return <div>Unknown Game Part</div>;
    }
  };

  return (
    <div className="client-view">
      {isAdmin && <AdminControls />}

      <div className="client-header">
        <div className="client-info">
          <span className="player-name">{playerName}</span>
          <span className="room-code">Room: {roomCode}</span>
          {playerId && (
            <span className="player-score">
              Score: {players.find(p => p.id === playerId)?.score || 0}
            </span>
          )}
        </div>
        <div className="header-buttons">
          {!isAdmin && (
            <button
              className="admin-toggle-button"
              onClick={() => setShowPasswordInput(!showPasswordInput)}
            >
              Admin Login
            </button>
          )}
          <button
            className="leave-button"
            onClick={() => setShowLeaveConfirm(true)}
          >
            Leave Game
          </button>
        </div>
      </div>

      {showLeaveConfirm && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3>Leave Game?</h3>
            <p>Are you sure you want to leave the game? You will be removed from the lobby.</p>
            <div className="modal-buttons">
              <button className="confirm-button" onClick={handleLeaveGame}>
                Yes, Leave
              </button>
              <button className="cancel-button" onClick={() => setShowLeaveConfirm(false)}>
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {showPasswordInput && !isAdmin && (
        <div className="password-modal">
          <div className="password-modal-content">
            <h3>Admin Login</h3>
            <h4 style={{ color: 'black' }}>HEY GET OUT OF HERE PLEASE DON'T BREAK ANYTHING</h4>
            <input
              type="password"
              value={passwordInput}
              onChange={(e) => setPasswordInput(e.target.value)}
              placeholder="seriously please dont guess"
              className="form-input"
              onKeyPress={(e) => e.key === 'Enter' && handleAdminLogin()}
            />
            <div className="password-modal-buttons">
              <button className="submit-button" onClick={handleAdminLogin}>
                Explode Phone (Don't Press!)
              </button>
              <button className="cancel-button" onClick={() => setShowPasswordInput(false)}>
                Return to Safety
              </button>
            </div>
          </div>
        </div>
      )}

      <div className="client-content">
        {renderContent()}
      </div>
    </div>
  );
};

export default ClientView;
