import React from 'react';
import { ImageDisplay as ImageDisplayType } from '../types';
import './ImageDisplay.css';

interface ImageDisplayProps {
    data: ImageDisplayType;
}

const ImageDisplay: React.FC<ImageDisplayProps> = ({ data }) => {
    return (
        <div className="image-display">
            <h1 className="image-display-title">{data.title}</h1>
            <img src={data.imageUrl} alt={data.title} className="image-display-image" />
        </div>
    );
};

export default ImageDisplay;
