import React from 'react';
import { MajorityQuestion, Player } from '../types';

interface MajorityResultsProps {
    data: MajorityQuestion;
    players: Player[];
}

const MajorityResults: React.FC<MajorityResultsProps> = ({ data, players }) => {
    // 1. Calculate stats
    const answerCounts = new Map<number, number>();
    const playerAnswers = new Map<number, Player[]>();

    let totalVotes = 0;

    players.filter(player => player.role !== 'admin').forEach(player => {
        if (typeof player.currentAnswer === 'number') {
            const count = answerCounts.get(player.currentAnswer) || 0;
            answerCounts.set(player.currentAnswer, count + 1);

            const pList = playerAnswers.get(player.currentAnswer) || [];
            pList.push(player);
            playerAnswers.set(player.currentAnswer, pList);

            totalVotes++;
        }
    });

    // 2. Prepare Pie Chart Segments
    // We'll use a simple CSS conic-gradient or SVG. SVG is cleaner for segments + labels.
    // Let's create an array of segments with start/end angles.
    let currentAngle = 0;
    const segments: {
        index: number;
        color: string;
        startAngle: number;
        endAngle: number;
        percentage: number;
        count: number;
        players: Player[];
    }[] = [];

    const COLORS = ['#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', '#FFEEAD', '#D4A5A5', '#9B59B6', '#3498DB'];

    data.options.forEach((option, index) => {
        const count = answerCounts.get(index) || 0;
        if (count > 0) {
            const percentage = count / totalVotes;
            const angleSpan = percentage * 360;
            segments.push({
                index,
                color: COLORS[index % COLORS.length],
                startAngle: currentAngle,
                endAngle: currentAngle + angleSpan,
                percentage,
                count,
                players: playerAnswers.get(index) || []
            });
            currentAngle += angleSpan;
        }
    });

    // Helper to get coordinates for pie slice labels/paths
    const getCoordinatesForPercent = (percent: number) => {
        const x = Math.cos(2 * Math.PI * percent);
        const y = Math.sin(2 * Math.PI * percent);
        return [x, y];
    };

    return (
        <div className="results-container majority-results">
            <div className="results-content" style={{ display: 'flex', gap: '2rem', justifyContent: 'center', alignItems: 'flex-start', flexWrap: 'wrap' }}>
                <div className="pie-chart-container" style={{ width: '300px', height: '300px', position: 'relative' }}>
                    <svg viewBox="-1 -1 2 2" style={{ transform: 'rotate(-90deg)' }}>
                        {segments.map((segment, i) => {
                            // Calculate path commands
                            const startX = Math.cos(segment.startAngle * Math.PI / 180);
                            const startY = Math.sin(segment.startAngle * Math.PI / 180);
                            const endX = Math.cos(segment.endAngle * Math.PI / 180);
                            const endY = Math.sin(segment.endAngle * Math.PI / 180);

                            // Flag for large arc (if > 180 degrees)
                            const largeArcFlag = segment.endAngle - segment.startAngle > 180 ? 1 : 0;

                            const pathData = [
                                `M 0 0`,
                                `L ${startX} ${startY}`,
                                `A 1 1 0 ${largeArcFlag} 1 ${endX} ${endY}`,
                                `Z`
                            ].join(' ');

                            return (
                                <path
                                    key={i}
                                    d={pathData}
                                    fill={segment.color}
                                    stroke="white"
                                    strokeWidth="0.01"
                                />
                            );
                        })}
                        {segments.length === 0 && (
                            <circle cx="0" cy="0" r="1" fill="#ddd" />
                        )}
                    </svg>
                </div>

                <div className="results-legend" style={{ textAlign: 'left', minWidth: '300px' }}>
                    {segments.sort((a, b) => b.count - a.count).map(segment => (
                        <div key={segment.index} className="legend-item" style={{ marginBottom: '1rem', borderLeft: `5px solid ${segment.color}`, paddingLeft: '10px' }}>
                            <div style={{ fontWeight: 'bold' }}>
                                {data.options[segment.index]}
                                <span style={{ marginLeft: '10px', fontSize: '0.9em', color: '#ffffffff' }}>
                                    ({Math.round(segment.percentage * 100)}% - {segment.count} votes)
                                </span>
                            </div>
                            <div className="player-list" style={{ fontSize: '0.85em', marginTop: '4px' }}>
                                {segment.players.map(p => (
                                    <span key={p.id} style={{ display: 'inline-block', marginRight: '5px', background: 'rgba(0,0,0,0.05)', padding: '2px 6px', borderRadius: '4px' }}>
                                        {p.name} (+{data.points * segment.percentage | 0})
                                    </span>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default MajorityResults;
