import React from 'react';
import { TitleCard as TitleCardType } from '../types';

interface TitleCardProps {
    data: TitleCardType;
}

const TitleCard: React.FC<TitleCardProps> = ({ data }) => {
    return (
        <div className="title-card">
            {data.imageUrl && <img src={data.imageUrl} alt={data.title} className="title-image" />}
            <h1>{data.title}</h1>
            {data.subtitle && <h2>{data.subtitle}</h2>}
        </div>
    );
};

export default TitleCard;
