import React, { useState, useEffect } from 'react';
import { SurveyQuestion, GameState } from '../types';
import { useGame } from '../contexts/GameContext';
import './NumberPickerDisplay.css'; // Reusing NumberPicker styles
import NumberResults from './NumberResults';
import TimerDisplay from './TimerDisplay';

interface SurveyDisplayProps {
    data: SurveyQuestion;
    state: GameState;
}

const SurveyDisplay: React.FC<SurveyDisplayProps> = ({ data, state }) => {
    const { submitAnswer, submitDraft, selectedAnswer, role, players } = useGame();
    const isClient = role === 'client';
    const isHost = role === 'host';
    const step = data.step || 1;

    // Use common min/max for both phases
    const minValue = data.minValue;
    const maxValue = data.maxValue;

    const [sliderValue, setSliderValue] = useState<number>(Math.floor((minValue + maxValue) / 2));
    const [inputValue, setInputValue] = useState<string>(sliderValue.toString());

    // Determine current phase details
    const isPollPhase = state === 'survey-poll-intro' || state === 'survey-poll-timer';
    const isGuessPhase = state === 'survey-guess-intro' || state === 'survey-guess-timer' || state === 'survey-guess-results' || state === 'survey-guess-results-reveal';

    const title = isPollPhase ? (data.pollTitle || 'Survey') : (data.guessTitle || 'Survey Results');
    const subtitle = isPollPhase ? data.pollSubtitle : data.guessSubtitle;
    const text = isPollPhase ? data.pollText : data.guessText;

    // Reset slider when phase changes/question changes effectively handled by key prop in parent or state reset
    useEffect(() => {
        // Updated to use float math if step is decimal
        setSliderValue(parseFloat(((minValue + maxValue) / 2).toFixed(1)));
        setInputValue(parseFloat(((minValue + maxValue) / 2).toFixed(1)).toString());
    }, [state, minValue, maxValue]);


    // Submit draft answer whenever slider value changes
    useEffect(() => {
        if (isClient && (state === 'survey-poll-timer' || state === 'survey-guess-timer') && selectedAnswer === null) {
            submitDraft(sliderValue);
        }
    }, [sliderValue, isClient, state, selectedAnswer, submitDraft]);

    const handleSliderChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = parseFloat(e.target.value);
        setSliderValue(value);
        setInputValue(value.toString());
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputValue(e.target.value);
    };

    const handleInputBlur = () => {
        let value = parseFloat(inputValue);
        if (isNaN(value)) value = sliderValue;
        else if (value < minValue) value = minValue;
        else if (value > maxValue) value = maxValue;

        // Ensure step adherence? Optional, but good practice.
        // For now just raw value.
        setSliderValue(value);
        setInputValue(value.toString());
    };

    const handleSubmit = () => {
        if (isClient && (state === 'survey-poll-timer' || state === 'survey-guess-timer') && (selectedAnswer === null || selectedAnswer === undefined)) {
            submitAnswer(sliderValue);
        }
    };

    const showControls = isClient && (selectedAnswer === null || selectedAnswer === undefined) && (state === 'survey-poll-timer' || state === 'survey-guess-timer');
    const showTimer = state === 'survey-poll-timer' || state === 'survey-guess-timer';

    // For NumberResults, we need to construct a "fake" NumberPickerQuestion object that matches the current state
    // specifically for the correct answer.
    // data.calculatedAverage is populated by backend in Phase 2.
    const resultsData = {
        ...data,
        type: 'number-picker-question' as const,
        correctAnswer: data.calculatedAverage || 0,
        text: data.guessText
    };

    const showReveal = state === 'survey-guess-results-reveal';

    return (
        <div style={{ background: 'rgba(0, 0, 0, 0)' }} className={`number-picker-display ${!isClient ? 'host-view' : ''}`}>
            <div className="number-picker-header">
                {title && <h1 className="survey-title" style={{ fontSize: '2em', marginBottom: '0.5em', color: '#ffb700' }}>{title}</h1>}
                {subtitle && <h3 className="survey-subtitle" style={{ marginBottom: '1em', opacity: 0.8 }}>{subtitle}</h3>}
                <h2 className="number-picker-text">{text}</h2>
                <div className="number-picker-meta">
                    <i>
                        {isGuessPhase && (
                            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginRight: '20px' }}>
                                <span>Earn Up to <b>{Math.floor(data.points * 1.5)}</b> Points</span>
                            </div>
                        )}
                        <span>Range: {minValue} to {maxValue}</span>
                    </i>
                </div>
            </div>

            {showTimer && (
                <TimerDisplay totalTime={data.timeLimit} />
            )}

            {showControls && (
                <div className="number-picker-controls">
                    <div className="current-value-display">
                        <h3>Your Answer: {sliderValue}</h3>
                    </div>

                    <div className="slider-container">
                        <span className="range-label">{minValue}</span>
                        <input
                            type="range"
                            min={minValue}
                            max={maxValue}
                            step={step}
                            value={sliderValue}
                            onChange={handleSliderChange}
                            className="number-slider"
                        />
                        <span className="range-label">{maxValue}</span>
                    </div>

                    <div className="input-container">
                        <label htmlFor="number-input">Or type a number:</label>
                        <input
                            id="number-input"
                            type="number"
                            min={minValue}
                            max={maxValue}
                            step={step}
                            value={inputValue}
                            onChange={handleInputChange}
                            onBlur={handleInputBlur}
                            className="number-input"
                        />
                    </div>

                    <button
                        className="submit-number-button"
                        onClick={handleSubmit}
                        disabled={!showTimer}
                    >
                        Submit Answer
                    </button>
                </div>
            )}

            {isClient && selectedAnswer !== null && selectedAnswer !== undefined && (
                <div className="number-picker-results">
                    <div className="answer-comparison">
                        <div className="answer-box">
                            <h4>Your Answer</h4>
                            <p className="answer-value">{selectedAnswer}</p>
                        </div>
                        {(state === 'survey-guess-results' || state === 'survey-guess-results-reveal') && (
                            <div className="answer-box correct">
                                <h4>Correct Answer</h4>
                                {/* Always show to client when results are in? Or wait for reveal? 
                                    User said "don't show the correct answer until the advance phase button is clicked".
                                    Usually this means on Host View. Clients usually see their own fate immediately.
                                    But "just like the regular number picker question" implies consistent behavior.
                                    NumberPickerDisplay shows correct answer in 'number-picker-results' AND 'number-picker-results-reveal'.
                                    So clients see it immediately. The Host hides it.
                                 */}
                                <p className="answer-value">{data.calculatedAverage}</p>
                            </div>
                        )}
                    </div>
                </div>
            )}

            {/* Host View - Phase 2 Results */}
            {!isClient && (state === 'survey-guess-results' || state === 'survey-guess-results-reveal') && (
                <NumberResults
                    data={resultsData as any}
                    players={players}
                    showCorrectAnswer={showReveal}
                />
            )}
        </div>
    );
};

export default SurveyDisplay;
