import React from 'react';
import { Player } from '../types';
import './PlayerAnswerNotification.css';

interface PlayerAnswerNotificationProps {
    recentAnswers: Array<{ playerId: string; timestamp: number }>;
    players: Player[];
}

const PlayerAnswerNotification: React.FC<PlayerAnswerNotificationProps> = ({
    recentAnswers,
    players,
}) => {
    // Map player IDs to names
    const getPlayerName = (playerId: string): string => {
        const player = players.find((p) => p.id === playerId);
        return player ? player.name : 'Unknown Player';
    };

    return (
        <div className="player-answer-notifications">
            {recentAnswers.map((answer) => (
                <div key={answer.timestamp} className="player-answer-notification">
                    <span className="player-name">{getPlayerName(answer.playerId)}</span>
                    <span className="answered-text">answered!</span>
                </div>
            ))}
        </div>
    );
};

export default PlayerAnswerNotification;
