import React from 'react';
import { Player } from '../types';
import { useGame } from '../contexts/GameContext';
import './PickOrderDisplay.css';

interface PickOrderDisplayProps {
    isHost: boolean;
}

export const PickOrderDisplay: React.FC<PickOrderDisplayProps> = ({ isHost }) => {
    const { gameState, pickOrderState, players, playerId, socket } = useGame();

    if (gameState !== 'pick-order' || !pickOrderState) return null;

    const { sortedPlayerIds, currentTurnIndex, takenPositions } = pickOrderState;
    const currentTurnPlayerId = sortedPlayerIds[currentTurnIndex];

    // Get all players for display mapping
    const playersMap = new Map(players.map(p => [p.id, p]));
    const currentTurnPlayer = playersMap.get(currentTurnPlayerId);
    const isMyTurn = playerId === currentTurnPlayerId;

    // Grid generation
    const totalPlayers = sortedPlayerIds.length;
    const positions = Array.from({ length: totalPlayers }, (_, i) => i + 1);

    const handlePick = (position: number) => {
        if (isHost) return;
        if (socket) {
            socket.emit('game:submit-pick', { position });
        }
    };

    if (isHost) {
        return (
            <div className="pick-order-container host-view" style={{ backgroundColor: 'rgba(0, 0, 0, 0)' }}>
                <h1 className="pick-order-title">White Elephant Pick Order</h1>
                <div className="pick-order-status">
                    {currentTurnIndex < sortedPlayerIds.length
                        ? `Waiting for ${currentTurnPlayer?.name} to pick...`
                        : "All picks complete!"}
                </div>

                <div className="pick-order-grid">
                    {positions.map(pos => {
                        const takerId = takenPositions[pos];
                        const taker = takerId ? playersMap.get(takerId) : null;

                        return (
                            <div
                                key={pos}
                                className={`pick-grid-item ${taker ? 'taken' : 'available'}`}
                            >
                                <div className="pick-number">{pos}</div>
                                {taker && <div className="pick-name">{taker.name}</div>}
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    }

    // CLIENT VIEW
    return (
        <div style={{ backgroundColor: 'rgba(0, 0, 0, 0)' }} className="pick-order-container client-view">
            <h2 className="pick-order-title">Pick Your Position</h2>

            {isMyTurn ? (
                <div className="turn-banner your-turn">It's your turn! Pick a spot!</div>
            ) : (
                <div className="turn-banner waiting">
                    Waiting for {currentTurnPlayer?.name} to pick...
                </div>
            )}

            <div className="pick-list-scroll">
                {positions.map(pos => {
                    const takerId = takenPositions[pos];
                    const isTaken = !!takerId;
                    const taker = takerId ? playersMap.get(takerId) : null;

                    return (
                        <button
                            key={pos}
                            className={`pick-list-item ${isTaken ? 'taken' : 'available'} ${isMyTurn && !isTaken ? 'selectable' : ''}`}
                            onClick={() => !isTaken && isMyTurn && handlePick(pos)}
                            disabled={isTaken || !isMyTurn}
                        >
                            <span className="pick-number">{pos}</span>
                            {isTaken ? (
                                <span className="pick-taker">Taken by {taker?.name}</span>
                            ) : (
                                <span className="pick-status">Available</span>
                            )}
                        </button>
                    );
                })}
            </div>
        </div>
    );
};
