import React, { useState, useEffect, useRef } from 'react';
import { PachinkoQuestion, GameState, Player } from '../types';
import { useGame } from '../contexts/GameContext';
import TimerDisplay from './TimerDisplay';
import './PachinkoDisplay.css';

interface PachinkoDisplayProps {
    data: PachinkoQuestion;
    state: GameState;
    players?: Player[];
}

interface Circle {
    id: string;
    playerName: string;
    x: number;
    y: number;
    vx: number;
    vy: number;
    color: string;
    slotIndex: number;
    landed: boolean;
    bucketIndex?: number;
}

interface Peg {
    x: number;
    y: number;
}

const PachinkoDisplay: React.FC<PachinkoDisplayProps> = ({ data, state, players = [] }) => {
    const { submitPachinkoResults, role } = useGame(); // Get role to check if host
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const [circles, setCircles] = useState<Circle[]>([]);
    const [pegs, setPegs] = useState<Peg[]>([]);
    const animationFrameRef = useRef<number>(0);
    const hasSubmittedRef = useRef<boolean>(false);

    const BOARD_WIDTH = 800;
    const BOARD_HEIGHT = 600;
    const CIRCLE_RADIUS = 8;
    const PEG_RADIUS = 4;
    const GRAVITY = 0.015; // Very slow, floaty descent
    const BOUNCE_DAMPING = 0.125; // Gentle bounces with some visible deflection
    const BUCKET_HEIGHT = 60;

    // Player colors
    const PLAYER_COLORS = [
        '#FF6B6B', '#4ECDC4', '#45B7D1', '#FFA07A',
        '#98D8C8', '#F7DC6F', '#BB8FCE', '#85C1E2'
    ];

    // Initialize pegs in a staggered pattern
    useEffect(() => {
        const newPegs: Peg[] = [];
        const rows = 12;
        const pegSpacing = 50;
        const startY = 100;

        for (let row = 0; row < rows; row++) {
            const pegsInRow = 10 + row;
            const rowWidth = pegsInRow * pegSpacing;
            const startX = (BOARD_WIDTH - rowWidth) / 2;
            const offsetX = row % 2 === 0 ? 0 : pegSpacing / 2;

            for (let col = 0; col < pegsInRow; col++) {
                newPegs.push({
                    x: startX + col * pegSpacing + offsetX,
                    y: startY + row * pegSpacing
                });
            }
        }

        setPegs(newPegs);
    }, []);

    // Initialize circles when state changes to results
    useEffect(() => {
        if (state === 'pachinko-results' && players) {
            hasSubmittedRef.current = false; // Reset submission flag
            const newCircles: Circle[] = [];

            players.forEach((player, index) => {
                if (typeof player.currentAnswer === 'number') {
                    const slotIndex = player.currentAnswer;
                    const slotWidth = BOARD_WIDTH / data.numSlots;
                    const x = slotWidth * slotIndex + slotWidth / 2;

                    newCircles.push({
                        id: player.id,
                        playerName: player.name,
                        x,
                        y: 50,
                        vx: (Math.random() - 0.5) * 0.5, // Slight initial jitter
                        vy: 0,
                        color: PLAYER_COLORS[index % PLAYER_COLORS.length],
                        slotIndex,
                        landed: false
                    });
                }
            });

            setCircles(newCircles);
        }
    }, [state, players, data.numSlots]);

    // Physics simulation
    useEffect(() => {
        if (state !== 'pachinko-results' || circles.length === 0) return;

        const animate = () => {
            setCircles(prevCircles => {
                const updatedCircles = prevCircles.map(circle => {
                    if (circle.landed) return circle;

                    let { x, y, vx, vy } = circle;

                    // Apply gravity
                    vy += GRAVITY;

                    // Update position
                    x += vx;
                    y += vy;

                    // Check collision with pegs
                    pegs.forEach(peg => {
                        const dx = x - peg.x;
                        const dy = y - peg.y;
                        const distance = Math.sqrt(dx * dx + dy * dy);

                        if (distance < CIRCLE_RADIUS + PEG_RADIUS) {
                            // Collision detected
                            const angle = Math.atan2(dy, dx);
                            const targetX = peg.x + Math.cos(angle) * (CIRCLE_RADIUS + PEG_RADIUS);
                            const targetY = peg.y + Math.sin(angle) * (CIRCLE_RADIUS + PEG_RADIUS);

                            // Bounce
                            const ax = targetX - peg.x;
                            const ay = targetY - peg.y;
                            vx = ax * BOUNCE_DAMPING;
                            vy = ay * BOUNCE_DAMPING;

                            // Add a little bit of noise to bounces to ensure randomness
                            vx += (Math.random() - 0.5) * 0.5;

                            x = targetX;
                            y = targetY;
                        }
                    });

                    // Bounce off walls (slightly more bouncy than pegs)
                    const WALL_BOUNCE_DAMPING = 0.15;
                    if (x - CIRCLE_RADIUS < 0) {
                        x = CIRCLE_RADIUS;
                        vx = -vx * WALL_BOUNCE_DAMPING;
                    } else if (x + CIRCLE_RADIUS > BOARD_WIDTH) {
                        x = BOARD_WIDTH - CIRCLE_RADIUS;
                        vx = -vx * WALL_BOUNCE_DAMPING;
                    }

                    // Check if landed in bucket
                    const bucketY = BOARD_HEIGHT - BUCKET_HEIGHT;
                    if (y + CIRCLE_RADIUS >= bucketY) {
                        const bucketWidth = BOARD_WIDTH / data.buckets.length;
                        const bucketIndex = Math.floor(x / bucketWidth);

                        return {
                            ...circle,
                            x,
                            y: bucketY - CIRCLE_RADIUS,
                            vx: 0,
                            vy: 0,
                            landed: true,
                            bucketIndex: Math.max(0, Math.min(data.buckets.length - 1, bucketIndex))
                        };
                    }

                    return { ...circle, x, y, vx, vy };
                });

                // Check if ALL circles have landed
                const allLanded = updatedCircles.every(c => c.landed);

                // If this is the first frame where all are landed, submit results
                // Only the host (or the creator of the room if host logic is client-side) should submit
                // Assuming 'host' role is set correctly in GameContext
                if (allLanded && prevCircles.length > 0 && !prevCircles.every(c => c.landed)) {
                    if (role === 'host' && !hasSubmittedRef.current) {
                        // Collect results
                        const results = updatedCircles.map(c => ({
                            playerId: c.id,
                            bucketIndex: c.bucketIndex!
                        }));

                        console.log('All pachinko balls landed. Host submitting results:', results);
                        submitPachinkoResults(results);
                        hasSubmittedRef.current = true;
                    }
                }

                return updatedCircles;
            });

            animationFrameRef.current = requestAnimationFrame(animate);
        };

        animationFrameRef.current = requestAnimationFrame(animate);

        return () => {
            if (animationFrameRef.current) {
                cancelAnimationFrame(animationFrameRef.current);
            }
        };
    }, [state, circles.length, pegs, data.buckets.length, role, submitPachinkoResults]);

    // Render canvas
    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        const ctx = canvas.getContext('2d');
        if (!ctx) return;

        // Clear canvas
        ctx.clearRect(0, 0, BOARD_WIDTH, BOARD_HEIGHT);

        // Draw buckets
        const bucketWidth = BOARD_WIDTH / data.buckets.length;
        const bucketY = BOARD_HEIGHT - BUCKET_HEIGHT;

        data.buckets.forEach((points, index) => {
            ctx.fillStyle = index % 2 === 0 ? '#34495e' : '#2c3e50';
            ctx.fillRect(index * bucketWidth, bucketY, bucketWidth, BUCKET_HEIGHT);

            ctx.strokeStyle = '#ecf0f1';
            ctx.lineWidth = 2;
            ctx.strokeRect(index * bucketWidth, bucketY, bucketWidth, BUCKET_HEIGHT);

            ctx.fillStyle = '#f39c12';
            ctx.font = 'bold 20px Arial';
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.fillText(`${points}`, index * bucketWidth + bucketWidth / 2, bucketY + BUCKET_HEIGHT / 2);
        });

        // Draw pegs
        pegs.forEach(peg => {
            ctx.beginPath();
            ctx.arc(peg.x, peg.y, PEG_RADIUS, 0, Math.PI * 2);
            ctx.fillStyle = '#95a5a6';
            ctx.fill();
            ctx.strokeStyle = '#7f8c8d';
            ctx.lineWidth = 1;
            ctx.stroke();
        });

        // Draw circles
        circles.forEach(circle => {
            ctx.beginPath();
            ctx.arc(circle.x, circle.y, CIRCLE_RADIUS, 0, Math.PI * 2);
            ctx.fillStyle = circle.color;
            ctx.fill();
            ctx.strokeStyle = '#2c3e50';
            ctx.lineWidth = 2;
            ctx.stroke();

            // Draw player name above circle
            ctx.fillStyle = '#2c3e50';
            ctx.font = 'bold 12px Arial';
            ctx.textAlign = 'center';
            ctx.fillText(circle.playerName, circle.x, circle.y - CIRCLE_RADIUS - 5);
        });

        // Draw drop slots
        const slotWidth = BOARD_WIDTH / data.numSlots;
        for (let i = 0; i <= data.numSlots; i++) {
            ctx.strokeStyle = '#bdc3c7';
            ctx.lineWidth = 1;
            ctx.setLineDash([5, 5]);
            ctx.beginPath();
            ctx.moveTo(i * slotWidth, 0);
            ctx.lineTo(i * slotWidth, 80);
            ctx.stroke();
            ctx.setLineDash([]);
        }

        // Draw slot numbers
        for (let i = 0; i < data.numSlots; i++) {
            ctx.fillStyle = '#7f8c8d';
            ctx.font = 'bold 16px Arial';
            ctx.textAlign = 'center';
            ctx.fillText(`${i + 1}`, i * slotWidth + slotWidth / 2, 20);
        }
    }, [circles, pegs, data.buckets, data.numSlots]);

    return (
        <div className="pachinko-display">
            <h2 className="pachinko-title">{data.text}</h2>

            {state === 'pachinko-intro' && (
                <div className="pachinko-intro">
                    <p>Players, select your drop slot!</p>
                    <p className="pachinko-instructions">Choose a number from 1 to {data.numSlots}</p>
                </div>
            )}

            {state === 'pachinko-timer' && (
                <div className="pachinko-waiting">
                    <p>Waiting for all players to select...</p>
                    <TimerDisplay totalTime={data.timeLimit} />
                </div>
            )}

            {state === 'pachinko-results' && (
                <div className="pachinko-board">
                    <canvas
                        ref={canvasRef}
                        width={BOARD_WIDTH}
                        height={BOARD_HEIGHT}
                        className="pachinko-canvas"
                    />
                </div>
            )}
        </div>
    );
};

export default PachinkoDisplay;
