import React from 'react';
import { MajorityQuestion as MajorityQuestionType, GameState } from '../types';
import { useGame } from '../contexts/GameContext';
import TimerDisplay from './TimerDisplay';
import MajorityResults from './MajorityResults';

interface MajorityQuestionDisplayProps {
    data: MajorityQuestionType;
    state: GameState;
}

const MajorityQuestionDisplay: React.FC<MajorityQuestionDisplayProps> = ({ data, state }) => {
    const { submitAnswer, selectedAnswer, role, players } = useGame();
    const isClient = role === 'client';

    const handleOptionClick = (index: number) => {
        if (isClient && state === 'majority-question-timer' && selectedAnswer === null) {
            submitAnswer(index);
        }
    };

    return (
        <div className="question-display">
            <div className="question-header">
                <h2 className="question-text">{data.text}</h2>
                <p className="majority-instruction" style={{ fontSize: '1.25rem', fontStyle: 'italic', marginBottom: '1rem', opacity: 0.9 }}>Pick the most popular answer!</p>
                <div style={{ fontSize: '1.24rem' }} className="question-meta">
                    <span><i>Up to <b>{data.points}</b> points (based on popularity)</i></span>
                    <TimerDisplay totalTime={data.timeLimit} />
                </div>
            </div>

            {(state === 'majority-question-intro' || state === 'majority-question-timer') && (
                <div className="answers-grid">
                    {data.options.map((option, index) => {
                        let className = 'answer-button';

                        if (selectedAnswer === index) {
                            className += ' selected';
                        }

                        // Only add disabled class for CLIENTS when they shouldn't be clicking
                        if (isClient && state !== 'majority-question-timer') {
                            className += ' disabled';
                        }

                        return (
                            <button
                                key={index}
                                className={className}
                                onClick={() => handleOptionClick(index)}
                                // Disable button functionality for host always, and for client when not in timer state or already answered
                                disabled={!isClient || state !== 'majority-question-timer' || selectedAnswer !== null}
                                // Override opacity for host via inline style or CSS class if 'disabled' prop forces it
                                style={!isClient ? { opacity: 1, cursor: 'default' } : {}}
                            >
                                {option}
                            </button>
                        );
                    })}
                </div>
            )}

            {/* Host Results View */}
            {!isClient && state === 'majority-question-results' && (
                <div className="host-majority-results" style={{ width: '100%', marginTop: '2rem' }}>
                    <MajorityResults data={data} players={players} />
                </div>
            )}

            {state === 'majority-question-results' && isClient && selectedAnswer !== null && (
                <div className="results-message">
                    <h3>You picked: {data.options[selectedAnswer as number]}</h3>
                </div>
            )}
        </div>
    );
};

export default MajorityQuestionDisplay;
