import React, { useState } from 'react';
import { useGame } from '../contexts/GameContext';
import TitleCard from './TitleCard';
import ImageDisplay from './ImageDisplay';
import QuestionDisplay from './QuestionDisplay';
import ColorQuestionDisplay from './ColorQuestionDisplay';
import MajorityQuestionDisplay from './MajorityQuestionDisplay';
import NumberPickerDisplay from './NumberPickerDisplay';
import MultiSelectDisplay from './MultiSelectDisplay';
import PachinkoDisplay from './PachinkoDisplay';
import Standing from './Standing';
import PlayerAnswerNotification from './PlayerAnswerNotification';
import { PickOrderDisplay } from './PickOrderDisplay';
import { WagerDisplay } from './WagerDisplay';
import SurveyDisplay from './SurveyDisplay';
import './HostView.css';

const HostView: React.FC = () => {
  const {
    roomCode,
    gameState,
    currentPart,
    nextPart,
    players,
    recentAnswers,
    createRoom,
    advanceGame,
    currentPartIndex,
    totalParts,
  } = useGame();
  const [isRoomCreated, setIsRoomCreated] = useState(false);

  const handleCreateRoom = () => {
    createRoom();
    setIsRoomCreated(true);
  };

  if (!isRoomCreated) {
    return (
      <div className="host-view">
        <div className="host-container">
          <h1>Host a Game</h1>
          <p className="host-description">
            Create a room and display this screen on your TV for all players to see.
          </p>
          <button className="create-room-button" onClick={handleCreateRoom}>
            Create Room
          </button>
        </div>
      </div>
    );
  }

  const renderContent = () => {
    if (gameState === 'waiting') {
      return (
        <div className="waiting-screen">
          <h2>Waiting for players...</h2>

          {players.length > 0 && (
            <div className="players-list">
              <h3>Connected Players ({players.length})</h3>
              <div className="players-grid">
                {players.map((player) => (
                  <div key={player.id} className="player-card">
                    <span className="player-name">{player.name}</span>
                    {player.role === 'admin' && (
                      <span className="player-badge admin-badge">Admin</span>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}

          {players.length === 0 && (
            <p className="no-players">No players connected yet</p>
          )}
        </div>
      );
    }

    if (gameState === 'wager') {
      return <WagerDisplay />;
    }

    if (!currentPart) return <div>Loading...</div>;

    switch (currentPart.type) {
      case 'titlecard':
        return <TitleCard data={currentPart} />;
      case 'image-display':
        return <ImageDisplay data={currentPart} />;
      case 'question':
        return <QuestionDisplay data={currentPart} state={gameState} />;
      case 'color-question':
        return <ColorQuestionDisplay data={currentPart} state={gameState} />;
      case 'majority-question':
        return <MajorityQuestionDisplay data={currentPart} state={gameState} />;
      case 'number-picker-question':
        return <NumberPickerDisplay data={currentPart} state={gameState} />;
      case 'multi-select-question':
        return <MultiSelectDisplay data={currentPart} state={gameState} />;
      case 'pachinko-question':
        return <PachinkoDisplay data={currentPart} state={gameState} players={players} />;
      case 'standing':
        return <Standing data={currentPart} players={players} />;
      case 'pick-order':
        return <PickOrderDisplay isHost={true} />;
      case 'survey-question':
        return <SurveyDisplay data={currentPart} state={gameState} />;
      default:
        return <div>Unknown Game Part: {(currentPart as any)?.type}</div>;
    }
  };

  return (
    <div className="host-view">
      <div className="host-header">
        <div className="room-code-display">
          <span className="room-code-label">Room Code:</span>
          <span className="room-code-value">{roomCode || 'Loading...'}</span>
        </div>
        <div className="question-counter" style={{ display: "none" }}>{currentPartIndex + 1}/{totalParts}</div>
      </div>

      <div className="host-content">
        {renderContent()}
      </div>

      <PlayerAnswerNotification recentAnswers={recentAnswers} players={players} />

      <div className="host-controls" style={{ display: "none" }}>
        <div className="next-up-preview">
          <span className="label">Next Up:</span>
          <span className="value">
            {nextPart
              ? `${nextPart.type.toUpperCase()} - ${'title' in nextPart ? nextPart.title : 'text' in nextPart ? nextPart.text : ''}`
              : 'End of Game'}
          </span>
        </div>
        <button className="advance-button" onClick={advanceGame}>
          Advance Game
        </button>
      </div>
    </div>
  );
};

export default HostView;
